<template>

    <div class="wrap">
        <div class="top">
            <el-input placeholder="搜索分类名称" prefix-icon="el-icon-search" v-model="input1" style="width:300px;">
            </el-input>
        </div>
        <div class="nav">
            <div :class="item.style" v-for="(item, index) in navs" :key="index" @click="trans(index, item)">{{ item.name
            }}
            </div>
        </div>
        <div class="table">
            <Table @abc="getnum"></Table>
        </div>
        <!-- 修改优先级 -->
        <el-dialog title="修改优先级" :visible.sync="dialogVisible" width="30%" :before-close="handleClose" align="left">
            <div class="kung">
                <div class="p">
                    <div class="span">原始优先级</div>
                    <el-input v-model="input2" disabled style="width:calc(100% - 90px)"></el-input>
                </div>
                <div class="p">
                    <div class="span">最新优先级</div>
                    <el-input v-model="input3" placeholder="请输入最新优先级" style="width:calc(100% - 90px)"></el-input>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import Table from "./components/table"
export default {
    beforeCreate() {

    },
    data() {
        return {
            navs: [
                {
                    name: "快购商城",
                    style: "act",
                },
                {
                    name: "网购商城",
                    style: "li",
                },
                {
                    name: "新分享商城",
                    style: "li",
                },
                {
                    name: "新分享兑换商城",
                    style: "li",
                },
                {
                    name: "券式团购",
                    style: "li",
                },
                {
                    name: "平台商家",
                    style: "li",
                },
                {
                    name: "聚宝盆",
                    style: "li",
                }
            ],
            input1: null,
            datafilter: '快购商城',
            tableData: null,
            dialogVisible: false,
            input2: null,
            input3: null,
        }
    },
    watch: {

    },
    methods: {
        trans(index, val) {
            this.navs.forEach(e => e.style = 'li')
            this.navs[index].style = 'act'
            this.datafilter = val.name
        },
        // 获取子组件数据，渲染修改模态框
        getnum(val) {
            this.input2 = val.num
            this.dialogVisible = true
        }

    },
    components: {
        Table
    }
}
</script>
<style lang="scss" scoped>
* {
    box-sizing: border-box;
}

.wrap {
    width: 100%;
    height: 100%;
    padding: 20px;

    .top {
        width: 100%;
        height: 60px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .nav {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 60px;
        border-bottom: 1px solid #ddd;

        .li {
            margin-right: 40px;
            height: 100%;
            display: flex;
            align-items: center;
        }

        .act {
            font-weight: 700;
            color: #fa4e21;
            border-bottom: 3px solid #fa4e21;
            margin-right: 40px;
            height: 100%;
            display: flex;
            align-items: center;
        }
    }

    .table {
        margin-top: 20px;
    }

    .p {
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 20px;

        .span {
            width: 90px;
        }

    }

}
</style>
