<template>
    <div>
        <el-table :data="tableData" style="width: 100%" border lazy :load="load" row-key="id"
            :tree-props="{ children: 'children', hasChildren: 'hasChildren' }">
            <el-table-column prop="name" label="分类名称" align="left" width="180">
            </el-table-column>
            <el-table-column label="排序" align="center">
                <template slot-scope="scope">
                    <div class="td">
                        <div :class="{ disabled: scope.row.num == 1, span: scope.row.num != 1 }">
                            <i class="el-icon-top"></i>
                        </div>&nbsp;&nbsp;&nbsp;
                        <div class="span">
                            <i class="el-icon-bottom"></i>
                        </div>
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="num" label="优先级" align="center">
            </el-table-column>
            <el-table-column prop="time" label="修改时间" align="center">
            </el-table-column>
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="editnum(scope.row)">修改优先级</el-button>
                    <el-button type="text" v-if="!scope.row.children">隐藏</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="footer">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="currentPage1" :page-size="4" layout="total, prev, pager, next"
                :total="this.datafilter.length">
            </el-pagination>
        </div>
    </div>
</template>
<script>
import { classifyManage } from '@/request/api'
export default {
    beforeCreate() {
        classifyManage().then(res => {
            this.netData = res
            this.tableData = res.filter(e => e.cate == this.datafilter)
        })
    },
    data() {
        return {
            netData: null,
            tableData: null,
            datafilter: "快购商城",
        }
    },
    watch: {
        "$parent.$data.datafilter"(a, b) {
            this.tableData = this.netData.filter(e => e.cate == a)
        }

    },
    methods: {
        editnum(val) {
            this.$emit('abc', val)
        }
    }
}
</script>
<style lang="scss" scoped>
.td {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .span {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 2px solid #333;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #333;
        font-size: 18px;
        font-weight: 700;
        cursor: pointer;
    }

    .disabled {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 18px;
        font-weight: 700;
        border: 2px solid #ccc;
        color: #ccc;
    }


}

.footer {
    margin-top: 50px;
}
</style>